  }

  .container {
      margin: 5px;
  }
  
  .row::after {
    content: "";
    clear: both;
    display: table;
  }
  
  [class*="col-"] {
    float: left;
    padding: 15px;
  }
    
  .header {
    background-color: #9933cc;
