.gallery-container {
    margin: 0px;
    padding: 0px;
    text-align: center;
    display: block;
    text-decoration: none;
    background-color: rgb(204, 196, 196);
    border-radius: 10px;
    width: 100%;
    height: auto;
}

.gallery-wrapper {
    margin: 5px;
    padding: 5px;
    width: 99%;
    height: auto;
    text-align: center;

}

.gallery-row{
    margin: 0 -5px 0 -5px;
}

.gallery-row::after {
    content: '';
    display: table;
    clear: both;
}

.gallery-col {
    float: left;
    width: 25%;
    padding: 0 5px;
}

.card {
    box-shadow: 0 4px 8px 0 rgba(51, 49, 49, 0.2);
    padding: 16px;
    margin-bottom: 5px;
    text-align: center;
    transition: -webkit-transform .1s;
    transition: transform .1s;
    transition: transform .1s, -webkit-transform .1s;
}

.gallery-a-effect {
    margin: 0px 0px 13px 0px;
}


.card:hover { /* IE 9 */
    -webkit-transform: scale(1.09); /* Safari 3-8 */
    transform: scale(1.09); 
}

