body {
  font-family: 'Roboto', sans-serif;
  background: #fff; /* fallback for old browsers */
}

* {
  margin: 0;
  padding: 0;
}

i {
  margin-right: 10px;
}

.navbar-logo {
  padding: 15px;
  color: #fff;
  text-decoration: none;
}

.navbar-logo:hover{
  color: rgb(58, 51, 51);
  border: 1px solid black;
  text-decoration: none;
  border-radius: 12px;
  font-size: 18px;
  margin: 10px;
  padding: 5px 10px 5px 12px;
  background: radial-gradient(circle, rgb(119, 119, 124)0%, rgb(155, 154, 158) 50%, rgb(213, 211, 223) 100%);
}

.nav-item:hover{
  background-color: rgb(62, 63, 63);
  border-radius: 10px;
}

.navbar-mainbg {
  background: radial-gradient(circle, rgb(54, 54, 56)0%, rgb(34, 32, 41) 50%, rgb(41, 39, 56) 100%);
  padding: 0px;
}

#navbarSupportedContent {
  overflow: hidden;
  position: relative;
}

#navbarSupportedContent ul {
  padding: 0px;
  margin: 0px;
}

#navbarSupportedContent ul li a i {
  margin-right: 10px;
}

#navbarSupportedContent li {
  list-style-type: none;
  float: left;
}

#navbarSupportedContent ul li a {
  color: rgba(255, 255, 255, 0.5);
  text-decoration: none;
  font-size: 15px;
  display: block;
  padding: 20px 20px;
  transition-duration: 0.6s;
  transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  position: relative;
}

#navbarSupportedContent > ul > li.active > a {
  color: #000000;
  background-color: transparent;
  transition: all 0.7s;
}

#navbarSupportedContent a:not(:only-child):after {
  content: '\f105';
  position: absolute;
  right: 20px;
  top: 10px;
  font-size: 14px;
  font-family: 'Font Awesome 5 Free';
  display: inline-block;
  padding-right: 3px;
  vertical-align: middle;
  font-weight: 900;
  transition: 0.5s;
}

#navbarSupportedContent .active > a:not(:only-child):after {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}


@media (max-width: 991px) {
  #navbarSupportedContent ul li a {
    padding: 12px 30px;
  }

  .nav-item:hover{
    background-color: rgb(62, 63, 63);
    border-radius: 10px;
  }

  .nav-link:hover{
    color: #fff;
  }
}
